@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
@import url(//maxcdn.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    outline: 0;
    font-family: 'Roboto', sans-serif;
}

body {
    height: 100%;
    background-color: white;
}

.container {
    position: relative;
}
header form .fa-search { 
  position: relative;
  top: 30px;
  left: 15px;
  font-size: 17px;
  z-index: 10;
}

header form .fa-search:hover {
    cursor: pointer;
    font-size: 20px;       
}

header {
    width: 100%;
    height: 48px;
    background-color: black;
    position: fixed;
    display: flex;
    z-index: 1000;
}

header h1 {
    color: #FFF;
    font-size: 16px;
    font-weight: normal;
    display: inline-block;
}

header div {
    background-color: black;
    width: 25%;
    height: 100%;
    max-height: 48px;
    display: flex;
    align-items: center;
}

header form {
    text-decoration: none;
    position: fixed;
    left: 380px;    
    width: 85%;
    align-self: center; 
    bottom: 95.5%;   
}

header .animacao {
    height: 16px;
    width: 20px;
    left: -20px;
    background: white;
    position: absolute;
    border-radius: 2px;
    transition: transform .2s ease-in-out;
}

.home {
    margin: 0px 8px;
    height: 30px;
    width: 30px;
}

.menu-icon {
    color: white;
    border-radius: 50%;
    height: 30px;
    width: 30px;
    cursor: pointer;
    transition: background .2s ease-in-out, color .2s ease-in-out, border-radius .2s;
}

.menu-icon:hover {
    background: #FFF;
    color: black;
    border-radius: 30%;
}

.home:hover + .animacao {
    transform: translateX(4px);
}

.link {
    text-decoration: none;
    position: absolute;
    right: 12px;
    align-self: center;
}

.link2{
    text-decoration: none;
    position: absolute;
    right: 210px;
    align-self: center;
}

.searchBar {
    position: relative;
    display: block;
    text-indent: 35px;
    background: #fff;
    border-radius: 31px;
    font-family: sans-serif;
    font-size: 20px;
    color: black;
    line-height: 1.2;    
    outline: none;
    border: none;    
}


.sidebar {
    background: #2b2b2b;
    height: calc(100vh - 48px);
    max-height: calc(100vh - 48px);
    width: 25%;
    position: fixed;
    top: 48px;
}

.content {
    overflow: auto;
    padding: 12px;
}

.content h1 {
    font-size: 24px;
    opacity: .8;
    border-bottom: 2px solid #000;
    margin-bottom: 12px;
    padding-bottom: 4px;
}

.content h2 {
    font-size: 16px;
    font-weight: normal;
    opacity: .6;
    padding-bottom: 8px;
}

ul.ks-cboxtags, ul.botoes-ordem {
    list-style: none;
}

ul.ks-cboxtags li{
  display: inline;
}

ul.ks-cboxtags li label{
    display: inline-block;
    background-color: none;
    font-weight: bold;
    border: 2px solid #ea6c75;
    border-radius: 25px;
    white-space: nowrap;
    margin: 2px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    transition: all .2s;
}

ul.ks-cboxtags li label {
    padding: 8px 12px;
    cursor: pointer;
}

ul.ks-cboxtags li input[type="checkbox"]:checked + label {
    border: 2px solid #ea6c75;
    background-color: #ea6c75;
    color: #fff;
    transition: all .2s;
}

ul.ks-cboxtags li input[type="checkbox"] {
  display: absolute;
}

ul.ks-cboxtags li input[type="checkbox"] {
  position: absolute;
  opacity: 0;
}

.preco-input {
    border: none;
    font-size: 16px;
    padding: 8px 8px 8px 16px;
    height: 32px;
    border-radius: 16px;
    width: 40%;
    transition: width .2s ease-in-out;
}

.preco-input::-ms-clear { 
    display: none;
}

.preco-input:focus {
    width: 60%;
}

.preco-input:focus + .label-preco {
    color: #000;
    left: -10%;
}

.preco-input:not(:-ms-input-placeholder) ~ .clear {
    display: inline-block;
}

.preco-input:not(:placeholder-shown) ~ .clear {
    display: inline-block;
}

.label-preco {
    color: rgba(0, 0, 0, 0.6);
    position: relative;
    left: -10%;
    transition: left .2s ease-in-out, color .2s ease-in-out;
    display: inline-block;
}

.clear {
    background: #FFF;
    color: rgba(0, 0, 0, 0.6);
    font-size: 20px;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    text-align: center;
    min-width: 0;
    vertical-align: baseline;
    margin: 0;
    padding: 0;
    position: relative;
    display: none;
    transition: background .2s ease-in-out, color .2s ease-in-out;
}

.clear:hover {
    background: #ea6c75;
    color: #FFF;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

h1.titulo-ordem {
    margin-top: 16px;
}

ul.botoes-ordem {
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
}

ul.botoes-ordem li {
    margin-bottom: 4px;
}

ul.botoes-ordem li button {
    margin: 0;
    min-width: 0;
    min-height: 0;
    color: #000;
    font-weight: bold;
    font-size: 16px;
    background: none;
    border: 2px solid #ea6c75;
    border-radius: 25px;
    display: flex;
    align-content: center;
    padding: auto;
    transition: all .3s ease-in-out;
}

ul.botoes-ordem li .selecionado {
    background: #ea6c75;
    color: #FFF;
}

ul.botoes-ordem li .icone {
    color: #FFF;
    font-weight: bold;
    display: inline-block;
    font-size: 20px;
    margin-left: 8px;
    transition: all .3s ease-in-out;
}

.bottom-nav {
    background: #ea6c75;
    height: 48px;
    width: 100%;
    position: absolute;
    bottom: 0;
    display: flex;
}

.nav-button {
    height: 100%;
    flex: 1 0 50%;
    padding: 8px 8px 0;
    cursor: pointer;
    transition: background .2s ease-in-out;
    display: flex;
    align-items: center;
    flex-direction: column;
    overflow: hidden;
    text-decoration: none;
    background-color: #2b2b2b;
}

.icon {
    height: 24px;
    width: 24px;
    color: #FFF;
    border-radius: 50%;
    transition: color .2s ease-in-out, background .2s ease-in-out;
}

.nav-button span {
    color: #FFF;
    font-size: 13px;
    transition: color .2s ease-in-out;
}

.nav-button h3 {
    color: #FFF;
    font-size: 18px;
    transition: color .2s ease-in-out;
}

.nav-button .animacao {
    background: #FFF;
    position: relative;
    bottom: -18px;
    height: 10px;
    width: 20px;
    border-radius: 4px;
    transition: transform .2s ease-in-out;
}

.nav-button:hover {
    background: black;
}

.nav-button:hover .icon{
    background: #FFF;
    color: white;
}

.nav-button:hover > .animacao {
    transform: translateY(-14px);
}
